nav{
    background: rgba(0,0,0,0.3);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 1.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}
nav a{
    background:transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: #fff;
    font-size: 1.1rem;
}
nav a:hover{
    background:  #4db5ff;
}

nav a.active{
    background:#1f1f38;
    color:#fff ;
}